'use client'
import { Boxes, ClipboardCheck, Cross, Leaf, LogOut, Menu, Package, Plus, PlusCircle, Search, ShoppingCartIcon, User, X, ChefHat, TrendingUp, MessageSquare } from 'lucide-react'

import Link from 'next/link'
import React, { FormEvent, useEffect, useRef, useState } from 'react'
import Image from 'next/image'
import { AnimatePresence, motion } from 'motion/react'
import { signOut } from 'next-auth/react'
import { createPortal } from 'react-dom'
import { useSelector } from 'react-redux'
import { RootState } from '@/redux/store'
import { useRouter } from 'next/navigation'
import LanguageSwitcher from './LanguageSwitcher'
import { useTranslations } from '@/i18n/LanguageProvider'
import { orbitron } from '@/lib/fonts'
import { ThemeToggle } from './ThemeToggle'

interface IUser {
    _id?: string
    name: string
    email: string
    password?: string
    mobile?: string
    role: "user" | "deliveryBoy" | "admin" | "farmer" | "shg" | "buyer" | "startup" | "processor"
    image?: string
}
function Nav({ user: propUser }: { user: any }) {
    const user = propUser || { name: 'Guest', email: '', role: 'user', image: null };
    const [open, setOpen] = useState(false)
    const profileDropDown = useRef<HTMLDivElement>(null)
    const [searchBarOpen, setSearchBarOpen] = useState(false)
    const [menuOpen, setMenuOpen] = useState(false)
    const { cartData } = useSelector((state: RootState) => state.cart)
    const [search, setSearch] = useState("")
    const router = useRouter()
    const t = useTranslations('common')
    const [scrolled, setScrolled] = useState(false)

    useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20)
        }
        window.addEventListener("scroll", handleScroll)
        return () => window.removeEventListener("scroll", handleScroll)
    }, [])

    useEffect(() => {
        const handleClickOutside = (e: MouseEvent) => {
            if (profileDropDown.current && !profileDropDown.current.contains(e.target as Node)) {
                setOpen(false)
            }
        }
        document.addEventListener("mousedown", handleClickOutside)
        return () => document.removeEventListener("mousedown", handleClickOutside)
    }, [])



    const handleSearch = (e: FormEvent) => {
        e.preventDefault()
        const query = search.trim()
        if (!query) {
            return router.push("/")
        }

        router.push(`/?q=${encodeURIComponent(query)}`)
        setSearch("")
        setSearchBarOpen(false)
    }

    const sideBar = menuOpen ? createPortal(
        <AnimatePresence>
            <motion.div
                initial={{ x: -100, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: -100 }}
                transition={{ type: "spring", stiffness: 100, damping: 14 }}
                className='fixed top-0 left-0 h-full w-[75%] sm:w-[60%] z-9999
              bg-linear-to-b from-green-800/90 via-green-700/80 to-green-900/90
              backdrop-blur-xl border-r border-green-400/20
              shadow-[0_0_50px_-10px_rgba(0,255,100,0.3)]
              flex flex-col p-6 text-white'
            >
                <div className='flex justify-between items-center mb-2'>
                    <h1 className='font-extrabold text-2xl tracking-wide text-white/90'>Admin Panel</h1>
                    <button className='text-white/80 hover:text-red-400 text-2xl font-bold transition'
                        onClick={() => setMenuOpen(false)}
                    ><X /></button>
                </div>
                <div className='flex items-center gap-3 p-3 mt-3 rounded-xl bg-white/10 hover:bg-white/15 transition-all shadow-inner'>
                    <div className='relative w-12 h-12 rounded-full overflow-hidden border-2 border-green-400/60 shadow-lg'> {user.image ? <Image src={user.image} alt='user' fill className='object-cover rounded-full' /> : <User />}</div>
                    <div >
                        <h2 className='text-lg font-semibold text-white'>{user.name}</h2>
                        <p className='text-xs text-green-200 capitalize tracking-wide'>{user.role}</p>
                    </div>
                </div>
                <div className='flex flex-col gap-3 font-medium mt-6'>
                    <Link href={"/admin/add-product"} className='flex items-center gap-3 p-3 rounded-lg bg-white/10 hover:bg-white/20 hover:pl-4 transition-all'><PlusCircle className='w-5 h-5' /> Add Product</Link>
                    <Link href={"/admin/view-products"} className='flex items-center gap-3 p-3 rounded-lg bg-white/10 hover:bg-white/20 hover:pl-4 transition-all'><Boxes className='w-5 h-5' /> View Products</Link>
                    <Link href={"/admin/manage-orders"} className='flex items-center gap-3 p-3 rounded-lg bg-white/10 hover:bg-white/20 hover:pl-4 transition-all'><ClipboardCheck className='w-5 h-5' /> Manage Orders</Link>
                </div>
                <div className='my-5 border-t border-white/20'></div>
                <div className='flex items-center gap-3 text-red-300 font-semibold mt-auto hover:bg-red-500/20 p-3 rounded-lg transition-all' onClick={async () => await signOut({ callbackUrl: "/" })}>
                    <LogOut className='w-5 h-5 text-red-300' />
                    Logout
                </div>
            </motion.div>
        </AnimatePresence>,
        document.body
    ) : null


    return (
        <nav className={`fixed left-1/2 -translate-x-1/2 z-50 flex justify-between items-center transition-all duration-700 ease-[cubic-bezier(0.23,1,0.32,1)] ${scrolled
            ? 'top-4 w-[92%] md:w-[85%] h-16 md:h-20 px-6 md:px-10 bg-white/60 dark:bg-zinc-900/60 backdrop-blur-2xl rounded-[2rem] border border-white/40 dark:border-zinc-800/50 shadow-[0_8px_32px_0_rgba(0,0,0,0.08)] dark:shadow-[0_8px_32px_0_rgba(0,0,0,0.4)]'
            : 'top-0 w-full h-24 px-8 md:px-16 bg-transparent border-transparent shadow-none'
            }`}>

            <Link href={"/"} className='flex items-center gap-2 md:gap-3 group hover:-translate-y-0.5 transition-transform duration-300'>
                <div className={`w-10 h-10 md:w-11 md:h-11 rounded-xl flex items-center justify-center shadow-lg transition-all duration-500 ${scrolled ? 'bg-gradient-to-br from-green-500 to-emerald-600 shadow-green-500/25' : 'bg-white/10 backdrop-blur-md border border-white/20 shadow-none'}`}>
                    <Leaf className={`${scrolled ? 'text-white' : 'text-green-400'} w-5 h-5 md:w-6 md:h-6 group-hover:scale-110 group-hover:rotate-6 transition-all duration-300`} />
                </div>
                <div className="flex flex-col">
                    <span className={`${orbitron.className} font-black text-lg sm:text-2xl tracking-tight group-hover:tracking-normal transition-all duration-500 ${scrolled ? 'text-zinc-800 dark:text-zinc-100' : 'text-white'}`}>
                        Agro<span className="relative inline-block">
                            w
                            <TrendingUp className="absolute -top-3 left-0 w-3 h-3 md:-top-3.5 md:w-4 md:h-4 text-green-500 transition-all duration-300" />
                        </span><span className={scrolled ? 'text-green-600' : 'text-green-400'}>Cart</span>
                    </span>
                    <span className={`hidden sm:inline-block text-[10px] font-medium tracking-widest uppercase transition-colors duration-500 ${scrolled ? 'text-zinc-400 dark:text-zinc-500' : 'text-zinc-300/80'}`}>Farm to Fork</span>
                </div>
            </Link>

            {user.role == "user" && (
                <form className={`hidden xl:flex items-center rounded-2xl px-5 py-2.5 w-1/3 max-w-lg focus-within:ring-2 focus-within:shadow-inner transition-all duration-500 border ${scrolled
                    ? 'bg-zinc-100/50 dark:bg-zinc-800/50 focus-within:bg-white dark:focus-within:bg-zinc-800 focus-within:ring-green-500/20 border-transparent focus-within:border-green-100 dark:focus-within:border-zinc-700'
                    : 'bg-white/10 backdrop-blur-md border-white/20 focus-within:bg-white/20 focus-within:ring-white/20'
                    }`} onSubmit={handleSearch}>
                    <Search className={`w-5 h-5 mr-3 transition-colors duration-500 ${scrolled ? 'text-zinc-400' : 'text-white/60'}`} />
                    <input
                        type="text"
                        placeholder='Search products...'
                        className={`w-full outline-none font-medium text-sm bg-transparent transition-colors duration-500 ${scrolled ? 'text-zinc-700 dark:text-zinc-200 placeholder-zinc-400' : 'text-white placeholder-white/60'}`}
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                    />
                </form>
            )}

            <div className='flex items-center gap-1.5 md:gap-3 relative'>
                {/* Community Recipes */}
                <Link href={"/recipes"} className={`relative rounded-xl w-9 h-9 md:w-11 md:h-11 flex items-center justify-center shadow-sm transition-all duration-500 group ${scrolled
                    ? 'bg-zinc-100 dark:bg-zinc-800/50 hover:bg-white dark:hover:bg-zinc-800 hover:ring-2 hover:ring-green-500/20'
                    : 'bg-white/10 backdrop-blur-sm border border-white/10 hover:bg-white/20'
                    }`} title="Community Recipes">
                    <ChefHat className={`w-4 h-4 md:w-5 md:h-5 group-hover:text-green-500 transition-colors ${scrolled ? 'text-zinc-600 dark:text-zinc-400' : 'text-white'}`} />
                </Link>

                <Link href={"/community/forum"} className={`relative rounded-xl w-9 h-9 md:w-11 md:h-11 flex items-center justify-center shadow-sm transition-all duration-500 group ${scrolled
                    ? 'bg-zinc-100 dark:bg-zinc-800/50 hover:bg-white dark:hover:bg-zinc-800 hover:ring-2 hover:ring-green-500/20'
                    : 'bg-white/10 backdrop-blur-sm border border-white/10 hover:bg-white/20'
                    }`} title="Discussion Forum">
                    <MessageSquare className={`w-4 h-4 md:w-5 md:h-5 group-hover:text-green-500 transition-colors ${scrolled ? 'text-zinc-600 dark:text-zinc-400' : 'text-white'}`} />
                </Link>

                {user.role == "user" && (
                    <>
                        <div className={`rounded-xl w-9 h-9 md:w-11 md:h-11 flex items-center justify-center shadow-sm transition-all duration-500 xl:hidden cursor-pointer ${scrolled
                            ? 'bg-zinc-100 dark:bg-zinc-800/50 hover:bg-white dark:hover:bg-zinc-800'
                            : 'bg-white/10 backdrop-blur-sm border border-white/10'
                            }`} onClick={() => setSearchBarOpen((prev) => !prev)}>
                            <Search className={`w-4 h-4 md:w-5 md:h-5 ${scrolled ? 'text-zinc-600 dark:text-zinc-400' : 'text-white'}`} />
                        </div>

                        <Link href={"/user/cart"} className={`relative rounded-xl w-9 h-9 md:w-11 md:h-11 flex items-center justify-center shadow-sm transition-all duration-500 group ${scrolled
                            ? 'bg-zinc-100 dark:bg-zinc-800/50 hover:bg-white dark:hover:bg-zinc-800 hover:ring-2 hover:ring-green-500/20'
                            : 'bg-white/10 backdrop-blur-sm border border-white/10 hover:bg-white/20'
                            }`}>
                            <ShoppingCartIcon className={`w-4 h-4 md:w-5 md:h-5 group-hover:text-green-500 transition-colors ${scrolled ? 'text-zinc-600 dark:text-zinc-400' : 'text-white'}`} />
                            {cartData.length > 0 && (
                                <span className='absolute -top-1 -right-1 bg-green-600 text-white text-[9px] md:text-[10px] w-4 h-4 md:w-5 md:h-5 flex items-center justify-center rounded-full font-bold shadow-lg border-2 border-white dark:border-zinc-900 animate-bounce'>
                                    {cartData.length}
                                </span>
                            )}
                        </Link>
                    </>
                )}

                <ThemeToggle />
                <LanguageSwitcher />

                <div className='relative' ref={profileDropDown}>
                    <motion.div
                        whileTap={{ scale: 0.95 }}
                        className='bg-linear-to-br from-green-500 to-emerald-700 p-[2px] rounded-2xl cursor-pointer shadow-lg shadow-green-500/10'
                        onClick={() => setOpen(prev => !prev)}
                    >
                        <div className='bg-white dark:bg-zinc-900 rounded-[14px] w-9 h-9 md:w-10 md:h-10 relative overflow-hidden flex items-center justify-center'>
                            {user.image ? (
                                <Image src={user.image} alt='user' fill className='object-cover' />
                            ) : (
                                <User className="text-green-600 w-4 h-4 md:w-5 md:h-5" />
                            )}
                        </div>
                    </motion.div>

                    <AnimatePresence>
                        {open && (
                            <motion.div
                                initial={{ opacity: 0, y: 10, scale: 0.95 }}
                                animate={{ opacity: 1, y: 0, scale: 1 }}
                                exit={{ opacity: 0, y: 10, scale: 0.95 }}
                                className='absolute right-0 mt-4 w-64 bg-white dark:bg-zinc-900 rounded-3xl shadow-2xl backdrop-blur-xl border border-zinc-100 dark:border-zinc-800 p-3 z-999 overflow-hidden'
                            >
                                <div className='flex items-center gap-4 px-4 py-4 mb-2 bg-linear-to-br from-green-50 to-emerald-50 dark:from-zinc-800 dark:to-zinc-800/50 rounded-2xl'>
                                    <div className='w-12 h-12 relative rounded-xl bg-white dark:bg-zinc-700 shadow-sm flex items-center justify-center overflow-hidden border border-green-100 dark:border-zinc-600'>
                                        {user.image ? <Image src={user.image} alt='user' fill className='object-cover' /> : <User className="text-green-600 dark:text-green-400" />}
                                    </div>
                                    <div className="overflow-hidden">
                                        <div className='text-zinc-900 dark:text-white font-bold truncate'>{user.name}</div>
                                        <div className='text-[10px] text-green-600 dark:text-green-400 font-black uppercase tracking-widest'>{user.role}</div>
                                    </div>
                                </div>

                                <div className="space-y-1">
                                    {/* Dashboard Links */}
                                    {["farmer", "shg", "processor"].includes(user.role) && (
                                        <Link href={`/${user.role}-dashboard`} className='flex items-center gap-3 px-4 py-3 hover:bg-zinc-50 dark:hover:bg-zinc-800 rounded-xl text-zinc-600 dark:text-zinc-400 font-semibold transition-colors' onClick={() => setOpen(false)}>
                                            <TrendingUp className='w-5 h-5 text-zinc-400' />
                                            Dashboard
                                        </Link>
                                    )}
                                    {user.role !== "user" && (
                                        <Link href={"/marketplace"} className='flex items-center gap-3 px-4 py-3 hover:bg-zinc-50 dark:hover:bg-zinc-800 rounded-xl text-zinc-600 dark:text-zinc-400 font-semibold transition-colors' onClick={() => setOpen(false)}>
                                            <Package className='w-5 h-5 text-zinc-400' />
                                            {t('visitMarketplace')}
                                        </Link>
                                    )}
                                    {user.role == "user" && (
                                        <Link href={"/user/my-orders"} className='flex items-center gap-3 px-4 py-3 hover:bg-zinc-50 dark:hover:bg-zinc-800 rounded-xl text-zinc-600 dark:text-zinc-400 font-semibold transition-colors' onClick={() => setOpen(false)}>
                                            <Package className='w-5 h-5 text-zinc-400' />
                                            {t('myOrders')}
                                        </Link>
                                    )}

                                    <button className='flex items-center gap-3 w-full text-left px-4 py-3 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-xl text-red-600 dark:text-red-400 font-semibold transition-colors' onClick={() => {
                                        setOpen(false)
                                        signOut({ callbackUrl: "/login" })
                                    }}>
                                        <LogOut className='w-5 h-5 text-red-400' />
                                        {t('logout')}
                                    </button>
                                </div>
                            </motion.div>
                        )}
                    </AnimatePresence>
                </div>
            </div>
            {sideBar}

            {/* Mobile Search Bar Overlay */}
            <AnimatePresence>
                {searchBarOpen && (
                    <motion.div
                        initial={{ opacity: 0, y: -10 }}
                        animate={{ opacity: 1, y: 0 }}
                        exit={{ opacity: 0, y: -10 }}
                        className='absolute top-24 left-0 w-full px-4 xl:hidden'
                    >
                        <form className='flex items-center bg-white/90 dark:bg-zinc-900/90 backdrop-blur-xl rounded-2xl px-5 py-3 shadow-2xl border border-zinc-100 dark:border-zinc-800' onSubmit={handleSearch}>
                            <Search className='text-zinc-400 w-5 h-5 mr-3' />
                            <input
                                type="text"
                                placeholder='Search products...'
                                className='w-full outline-none text-zinc-700 dark:text-zinc-200 placeholder-zinc-400 bg-transparent font-medium text-sm'
                                value={search}
                                onChange={(e) => setSearch(e.target.value)}
                                autoFocus
                            />
                        </form>
                    </motion.div>
                )}
            </AnimatePresence>
        </nav>
    )
}

export default Nav
